import React from "react";
import "./home.css";
import breadStore from "./pic/breadStore.jpg";
import bread from "./pic/bread.jpg";
import whiteroseRest from "./pic/whiteroseRest.jpg";
import whiterose from "./pic/whiterose.jpg";
import wantan from "./pic/wantan.jpg";
import mot from "./pic/mot.jpg";
import motStore from "./pic/motStore.jpg";
import kaurau from "./pic/kaurau.jpg";
import cafe1 from "./pic/cafe1.jpg";
import cocobox from "./pic/cocobox.jpg";
import cocoboxStore from "./pic/cocoboxStore.jpg";
import chef from "./pic/chef.jpg";
import chefStore from "./pic/chefStore.jpg";

const Foods = () => {
  return (
    <div className="margin">
      <h1 className="tab-header">食べ物・お店</h1>
      <p>ホイアンの美味しい食べ物や、おススメのお店を紹介していきます</p>
      <div className="introduce">
        <h2 className="intro-header">その1 マダムカーンザバインミークイーン</h2>
        <img src={breadStore} className="pic" style={{ width: "40%" }} />
        <img src={bread} className="pic" style={{ width: "40%" }} />
        <p>説明</p>
      </div>
      <div className="introduce">
        <h2 className="intro-header">その2 ホワイトローズ</h2>
        <img src={whiteroseRest} className="pic" />
        <img src={whiterose} className="pic" />
        <img src={wantan} className="pic" />
        <p>説明</p>
      </div>
      <div className="introduce">
        <h2 className="intro-header">その3 Mot Hoi An</h2>
        <img src={motStore} className="pic" style={{ width: "20%" }} />
        <img src={mot} className="pic" style={{ width: "30%" }} />
        <img src={kaurau} className="pic" style={{ width: "20%" }} />
        <p>説明</p>
      </div>
      <div className="introduce">
        <h2 className="intro-header">その4 Cocobox</h2>
        <img src={cocoboxStore} className="pic" />
        <img src={cocobox} className="pic" />
        <img src={cafe1} className="pic" style={{ width: "20%" }} />
        <p>説明</p>
      </div>
      <div className="introduce">
        <h2 className="intro-header">その5 The Chef</h2>
        <img src={chefStore} className="pic" />
        <img src={chef} className="pic" style={{ width: "30%" }} />
        <p>説明</p>
      </div>
      <p>各お店の場所はこちら</p>
      <iframe
        src="https://www.google.com/maps/d/embed?mid=1bvP5mcdx-1DkCXawp32_52ev3T57wcLT"
        width="100%"
        height="480"
      ></iframe>
    </div>
  );
};

export default Foods;
